.App {
  /*Estilos Generales de Color*/
  --color-principal: #1DBEB4;
  --color-oscuro: #383B58;
  --color-claro: #545776;
  --color-fondo: #F3F1ED;

  font-family: 'Roboto', sans-serif;
  text-align: center;
}

/*Estilos Generales de Texto*/
.Heading-1 {
  font-weight: bold;
  font-size: 24px;
}

.Heading-2 {
  font-weight: bold;
  font-size: 20px;
}

.Heading-3 {
  font-weight: bold;
  font-size: 16px;
}

.Heading-4 {
  font-weight: bold;
  font-size: 14px;
}
.Button-1 {
  font-weight: bold;
  font-size: 14px;
}

.Button-2 {
  font-weight: bold;
  font-size: 16px;
}
.Text-1 {
  font-weight: 500;
  font-size: 14px;
}

.Text-2 {
  font-weight: 500;
  font-size: 12px;
}



