.SearchBar-container {
  background-color: var(--color-claro);
  padding-top: 2px;
  height: fit-content;
  padding-bottom: 26px;
  padding-left: 30px;
  padding-right: 30px;
}

.SearchBar-container .SearchBar-inputs {
  display: flex;
  flex-basis: 100%;
}

.SearchBar-container .SearchBar-block1 {
  display: flex;
  flex-basis: 100%;
}

.SearchBar-container .SearchBar-block2 {
  display: flex;
  width: 200px;
}

.SearchBar-container > h1 {
  color: #fff;
  font-weight: 700;
  font-size: 36px;
}

.SearchBar-container select {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  margin-right: 8px;
  font-weight: 500;
  font-family: Roboto;
  font-size: 14px;
  color: #7f7f7f;
}

.SearchBar-container .search-calendar {
  position: absolute;
  z-index: 15;
  margin-top: 7px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 5px 5px;
}

.SearchBar-container .search-calendar > Button {
  margin-bottom: 18px;
  margin-right: 31px;
}

/* clases para customizar el calendario */

.SearchBar-container .calendar-custom .react-calendar__tile {
  font-weight: 400;
  font-family: Roboto;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.87);
}

.SearchBar-container .calendar-custom .react-calendar__navigation__label {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.SearchBar-container .calendar-custom .react-calendar__navigation__next2-button,
.SearchBar-container .calendar-custom .react-calendar__navigation__prev2-button {
  display: none;
}

.SearchBar-container .calendar-custom .react-calendar__tile--now {
  background-color: transparent;
}

.SearchBar-container .calendar-custom .react-calendar__tile--active {
  background-color: transparent;
}

.SearchBar-container .calendar-custom .react-calendar__tile--active > abbr {
  background-color: var(--color-principal);
  border-radius: 50%;
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 700;
}

.SearchBar-container .calendar-custom .react-calendar__tile--active:enabled:hover,
.SearchBar-container .react-calendar__tile--active:enabled:focus {
  background-color: transparent !important;
}

.SearchBar-container .calendar-custom abbr[title] {
  text-decoration: none;
}

.SearchBar-container .react-calendar {
  border: 0 !important;
}

.SearchBar-container .calendar-custom .react-calendar__navigation__label__labelText {
  font-family: Roboto;
  font-weight: 500;
  font-size: 14px;
  color: #000000;
}

.SearchBar-container div:first-child.react-calendar__month-view:after {
  border-right: 1px solid #a8a8a8;
  display: inline-block;
  height: 100%;
  width: 1px;
  content: "";
  margin-left: 15px;
}

.SearchBar-container div:first-child.react-calendar__month-view {
  display: flex;
}

.SearchBar-container .calendar-custom .react-calendar__navigation__label__divider {
  display: none;
}

.SearchBar-container .calendar-custom .react-calendar__tile--now:enabled:hover {
  background-color: transparent;
}

.SearchBar-container .react-calendar__month-view__days__day--neighboringMonth {
  visibility: hidden !important;
}

.SearchBar-container .search-calendar.simple-calendar{
  display: none;
}

/* selectores de inputs */

.SearchBar-container .searchBar-calendar {

  width: 100%;
  height: 40px;
  border-radius: 5px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  margin-right: 8px;
  font-weight: 500;
  font-family: Roboto;
  font-size: 14px;
  color: #7f7f7f;
  background-color: #fff;
  display: flex;
}

.SearchBar-container .searchBar-calendar img {
  padding: 9px;
}

.SearchBar-container .searchBar-calendar input,
.SearchBar-container .searchBar-calendar input:focus-visible {
  border: none;
  font-family: inherit;
  outline: none;
  color: var(--color-claro);
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .SearchBar-container > select {
    width: 254px;
  }
}

@media screen and (max-width: 767px) {
  .SearchBar-container .SearchBar-inputs {
    flex-direction: column;
  }

  .SearchBar-container .SearchBar-container {
    padding: 10px;
  }

  .SearchBar-container .SearchBar-block1 {
    flex-direction: column;
  }

  .SearchBar-container .SearchBar-container select,
  .SearchBar-container .SearchBar-container button {
    margin: 4px 0;
  }

  .SearchBar-container .SearchBar-container .SearchBar-block2,
  .SearchBar-container .SearchBar-container button.btn {
    width: 100%;
  }
  
  .SearchBar-container .SearchBar-block2 {
    display: flex;
    width: 100%;
  }
  

  .SearchBar-container .SearchBar-container h1 {
    font-size: 30px;
  }

  .SearchBar-container .SearchBar-container select {
    width: 100%;
    height: 38px;
    margin-right: 0;
  }

  .SearchBar-container .search-calendar.double-calendar {
    display: none;
  }

  .SearchBar-container .search-calendar.simple-calendar {
    display: block;
    padding: 20px;
    margin-right: 10px;
  }

  .SearchBar-container .calendar-custom.react-calendar {
    width: 100%;
  }

  .SearchBar-container .searchBar-calendar {
    margin-bottom: 6px;
  }

}
