section>h2 {
    text-align: left;
    padding: 2%;
    margin: 0;
    color: var(--color-oscuro);
    font-weight: bold;
}

section {
    padding: 0 1%;
    box-sizing: border-box;

    display: flex;
    flex-flow: column nowrap;
}


.sec__categories {
    background-color:white !important;
}

.sec__products {
    background-color: var(--color-fondo);
}

@media (max-width: 1400px) {
    .cat__container {
        display: flex;
        justify-content: center;
    }
}
