.categories__list {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    margin: 0;
    padding: 0;
    box-sizing: border-box;

    width: 100%;
}

.categories__list li {
    list-style-type: none;
    display: flex;
    padding: 2% 1%;
}

@media (max-width: 1080px) {
    .categories__list {
        flex-wrap: wrap;
        max-width: 950px;
    }

}