.card__categories {
    border-radius: 15px;
    box-shadow: 4px 5px 7px -1px rgba(0, 0, 0, 0.6);
}

.card__categories-div1 {
    flex-shrink: 0;

}

.card__categories-div1 img {
    width: 20vw;
    height: 200px;
    object-fit: cover;
    border-radius: 15px 15px 0 0;
}

.card__categories-div2 {
    box-sizing: border-box;
    padding: 0 2%;
    width: 20vw;
    text-align: left;
}

.card__categories-div2 h4 {
    padding: 2%;
    margin: 0;
    color: var(--color-oscuro);
    font-weight: bold;
}

.card__categories-div2 p {
    color: var(--color-oscuro);
    opacity: 50%;
    font-weight: bold;
    padding: 2%;
    margin: 0;
}


@media (max-width: 1080px) {

    .card__categories-div1 img {
        width: 340px;
    }

    .card__categories-div2 {
        width: 340px;
    }

}