.card__product {
    background-color: white;
    height: 250px;
    display: flex;
    border-radius: 2px;
    border-radius: 15px;
    box-shadow: 4px 5px 7px -1px rgba(0, 0, 0, 0.6);
}

.card__product-div1 {
    flex-shrink: 0;
    border-radius: 2px;
    /* para acomodar el icon */
    position: relative;
}

.card__product-div2 {
    box-sizing: border-box;
    padding: 10px;
    width: 20vw;
    text-align: left;
    position: relative;
    min-width: 280px;
}

/* subdivisiones */
.card__product-div1 .fav__icon {
    position: absolute;
    z-index: 10;
    right: 5%;
    top: 5%;
}

.card__product-div1 img {
    width: 20vw;
    height: 250px;
    object-fit: cover;
    border-radius: 15px;
}

.card__product-div2 .loc__icon {
    padding-right: 3px;
    margin-bottom: -3px;
}

.card__product-div2 .start__icon {
    margin-bottom: -5px;
}


.card__product-div2 h4 {
    margin: 0;
    font-size: 14px;
    display: inline-block;
}

.card__product-div2 .card__category {
    color: var(--color-claro);
    opacity: 50%;
}

.card__product-div2 h2 {
    color: var(--color-oscuro);
    margin-top: 5px;
    margin-bottom: 0;
    padding-bottom: 8px;
    font-size: 17px;
    max-height: 25px;
}

.card__product-div2 .card__desc {
    margin-top: 20px;
}

.card__product-div2 .more {
    color: var(--color-principal);
    font-weight: bold;
    text-decoration: none;
}

.ws__icon {
    margin-top: -5px;
}

.card__stats {
    display: flex;
    justify-content: space-between;
}

.card__title {
    width: 67%;
}

.card__cali {
    padding-top: 5px;
}

.card__loc {
    margin: 7px 0;
}

.card__starts {
    margin-bottom: 1px;
}

.card__number {
    background-color: var(--color-oscuro);
    color: var(--color-fondo);
    font-weight: bold;
    padding: 3px 10px;
    border-radius: 8px;
    line-height: 1px;
}

.card__review {
    color: var(--color-oscuro);
    font-weight: bold;
    font-size: 13px;
    margin: 9px 0 0 0;
}


.txt-12 {
    font-size: 12px;
}

.card__button {
    position: absolute;
    bottom: 3%;
    width: 93%;
}


@media (max-width: 1080px) {

    .card__product {
        height: 250px;
    }

    .card__product-div1 {
        flex-shrink: 0;
    }

    .card__product-div2 {
        padding: 10px;
        width: 45vw;
    }

    .card__product-div1 img {
        width: 45vw;
        height: 250px;
    }

}

@media (max-width: 550px) {

    .card__product {
        height: 500px;
        flex-direction: column;
    }

    .card__product-div2 {
        height: 250px;
        width: 90vw;
    }

    .card__product-div1 img {
        width: 90vw;
        border-radius: 10px 10px 0px 0px;
    }

}