.product__list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;

    margin: none;
    padding: 0;
    box-sizing: border-box;
}

.product__list li {
    list-style-type: none;
    display: flex;
    padding-bottom: 20px;
}


@media (max-width: 550px) {
    
    .product__list li {
        height: 500px;
        width: 100%;
        justify-content: center;
    }

}
