.carousel-container {
    margin-top: 5%;
    width: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.carousel-wrapper {
    display: flex;
    width: 100%;
    height: 100%;
    position: relative;
}

.carousel-content-wrapper {
    overflow: hidden;
    width: 90%;
    height: 90%;
    margin: auto;
    
}

.carousel-content {
    display: flex;
    transition: all 250ms linear;
    -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
    scrollbar-width: none;  /* hide scrollbar in Firefox */
}

/* hide scrollbar in webkit browser */
.carousel-content::-webkit-scrollbar, .carousel-content::-webkit-scrollbar {
    display: none;
}

.carousel-content > * {
    width: 100%;
    margin:auto;
    border-radius: 10px;
    flex-shrink: 0;
    flex-grow: 1;
}

.left-arrow, .right-arrow {
    position: absolute;
    z-index: 1;
    top: 50%;
    width: 40px;
    height: 40px;
    border-radius: 24px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
}

.left{
    left: 10px;
}

.right-arrow {
    right: 10px;
}